export const renderMainUI = (mode) => `
  <div class="clicky-header">
    <span>Clicky AI</span>
    <span id="clicky-toggle">_</span>
  </div>
  <div class="clicky-body" id="clicky-results">
    <p>Hello! I'm Clicky. Ask me anything about foundry products.</p>
    <div id="clicky-suggestions"></div>
  </div>
  <div class="clicky-input-area">
    <input type="text" class="clicky-input" id="clicky-input" placeholder="Ask a question..." />
    <button class="clicky-btn" id="clicky-send">Go</button>
  </div>
`;

export const renderLoading = (id) => `<p id="${id}" style="color:#666; font-style:italic;">Thinking...</p>`;

export const renderError = (msg) => {
    const p = document.createElement('p');
    p.className = 'clicky-error';
    p.textContent = msg;
    return p;
};

export const renderSummary = (text) => {
    const div = document.createElement('div');
    Object.assign(div.style, {
        marginTop: '10px',
        padding: '8px',
        background: '#eef',
        borderRadius: '4px'
    });
    div.textContent = text;
    return div;
};

export const renderSuggestions = (suggestions, onClick) => {
    const container = document.createElement('div');
    container.style.marginTop = '10px';
    suggestions.forEach(s => {
        const btn = document.createElement('button');
        btn.textContent = s;
        Object.assign(btn.style, {
            margin: '2px',
            fontSize: '0.8em',
            background: 'none',
            border: '1px solid #ccc',
            borderRadius: '4px',
            cursor: 'pointer',
            padding: '2px 6px'
        });
        btn.onclick = () => onClick(s);
        container.appendChild(btn);
    });
    return container;
};
