import { ApiClient } from '../api/api.js';
import { styles } from '../ui/styles.js';
import { renderMainUI, renderLoading, renderError, renderSummary, renderSuggestions } from '../ui/templates.js';

export class ClickyCore {
    constructor(config) {
        this.config = null;
        this.api = null;
        this.shadowRoot = null;

        if (config) {
            this.init(config);
        }
    }

    init(config) {
        if (this.config) {
            console.warn('Clicky already initialized');
            return;
        }

        if (!config || !config.token) {
            console.error('Clicky: Auth token is required.');
            return;
        }

        this.config = {
            mode: 'mini',
            serverUrl: 'http://localhost:5003',
            ...config
        };

        this.api = new ApiClient(this.config.serverUrl, this.config.token);
        console.log('Clicky Core Initialized');
        this.mount();
    }

    mount() {
        // Host
        const host = document.createElement('div');
        host.id = 'clicky-host';
        document.body.appendChild(host);
        this.shadowRoot = host.attachShadow({ mode: 'open' });

        // Styles
        const styleTag = document.createElement('style');
        styleTag.textContent = styles;
        this.shadowRoot.appendChild(styleTag);

        // UI Wrapper
        const wrapper = document.createElement('div');
        wrapper.className = `clicky-ui ${this.config.mode}`;
        wrapper.innerHTML = renderMainUI(this.config.mode);
        this.shadowRoot.appendChild(wrapper);

        // Event Bindings
        this.bindEvents(wrapper);
    }

    bindEvents(wrapper) {
        const input = this.shadowRoot.getElementById('clicky-input');
        const sendBtn = this.shadowRoot.getElementById('clicky-send');
        const toggleBtn = this.shadowRoot.getElementById('clicky-toggle');

        sendBtn.addEventListener('click', () => this.handleQuery(input.value));
        input.addEventListener('keypress', (e) => {
            if (e.key === 'Enter') this.handleQuery(input.value);
        });

        toggleBtn.addEventListener('click', () => {
            wrapper.classList.toggle('mini');
            wrapper.classList.toggle('minimized');
            if (wrapper.classList.contains('minimized')) {
                wrapper.style.height = '40px';
            } else {
                wrapper.style.height = '';
            }
        });
    }

    async handleQuery(queryText) {
        const input = this.shadowRoot.getElementById('clicky-input');
        const results = this.shadowRoot.getElementById('clicky-results');
        const query = queryText.trim();

        if (!query) return;

        // UI Optimistic Update
        const loadingId = 'loading-' + Date.now();
        results.insertAdjacentHTML('beforeend', renderLoading(loadingId));
        results.scrollTop = results.scrollHeight;
        input.value = '';

        try {
            const data = await this.api.query(query, this.config.context);

            // Remove loader
            const loader = this.shadowRoot.getElementById(loadingId);
            if (loader) loader.remove();

            // Render HTML
            results.insertAdjacentHTML('beforeend', data.html);

            // Render Summary
            if (data.summary) {
                results.appendChild(renderSummary(data.summary));
            }

            // Render Suggestions
            if (data.suggestions && data.suggestions.length) {
                results.appendChild(renderSuggestions(data.suggestions, (txt) => {
                    input.value = txt;
                    this.handleQuery(txt);
                }));
            }

        } catch (err) {
            console.error(err);
            const loader = this.shadowRoot.getElementById(loadingId);
            if (loader) loader.remove();
            results.appendChild(renderError('Connection failed.'));
        }

        results.scrollTop = results.scrollHeight;
    }
}
